@use '../styles/colors.scss';

* {
  box-sizing: border-box;
}

html {
  width: 100vw;
}

body {
  overflow-x: hidden;
  font-family: 'Metropolis';
  color: black;
  margin: unset;
}

a:link,
a:visited {
  text-decoration: inherit;
  color: inherit;
  cursor: pointer;
}

.text-center {
  text-align: center;
}

//Change grey-blue -> cadet
//change orange-brown -> orange

.button-cadet,
.button-orange {
  border: none;
  color: #ffffff;
  border-radius: 3px;
  font-size: 16px;
}

.button-cadet:hover,
.button-orange:hover {
  cursor: pointer;
}

.button-cadet {
  background: #7f9296;
}

.button-cadet:hover {
  background: #707f82;
}

.button-orange {
  background: #ddb091;
}

.button-orange:hover {
  background: #cfa588;
}

/* Removing arrows off number inputs */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}
